.start {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.start h4 {
  font-size: 40px;
  font-weight: 100;
  text-align: center;
  font-family: 'Amatic SC', cursive;
}

.start h2 {
  font-size: 140px;
  font-weight: 100;
  text-align: center;
  font-family: 'Amatic SC', cursive;
  margin: 0px;
  cursor: pointer;
}

.toucan {
  height: 20vh;
  position: fixed;
  top: 20vh;
}

.doge {
  height: 20vh;
  position: fixed;
  bottom: 20vh;
  right: 0;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.raceWrapper {
  height: 90vh;
  width: 100vw;
  position: fixed;
}

.snail {
  height: 15vh;
  position: relative;
  top: 1vh;
  cursor: none;
}

.finish {
  height: 10vh;
  position: fixed;
  bottom: 5vh;
  right: 10vw;
  padding: 80px;
}

.moleContainer {
  height: 90vh;
  width: 100vw;
  position: fixed;
}

.mole {
  height: 15vh;
  position: fixed;
  cursor: crosshair;
  user-select: none;
}

.ostrich {
  height: 15vh;
  position: fixed;
  cursor: crosshair;
  user-select: none;
  /* pointer-events: none; */
}

.click {
  width: 10vw;
  opacity: 0.5;
  position: fixed;
  left: 45vw;
  top: 40vh;
  animation-name: click;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  pointer-events: none;
}

@keyframes click {
  0% {left: 18vw; top: 25vh}
  50% {left: 25vw; top: 30vh}
  100% {left: 18vw; top: 25vh}
}

.exampleMole {
  height: 200px;
  opacity: 0.5;
}

.tick {
  height: 100px;
  margin-right: 40px;
  opacity: 0.5;
  animation-name: pulse;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.cross {
  height: 100px;
  margin-left: 40px;
  opacity: 0.5;
  animation-name: pulse;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@keyframes pulse {
  0% {height: 100px}
  50% {height: 130px}
  100% {height: 100px}
}

.exampleContainer {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  height: 200px;
  margin-top: 200px;
  width: 100%;
}

.imgContainer {
  display: flex;
  justify-content: center;
}

.monster {
  position: fixed;
  bottom: 0;
  right: 0;
  height: 40vh;
}

.animal {
  height: 20vh;
  position: fixed;
  z-index: 2;
  cursor: none;
}

.mouseIcon {
  width: 15vw;
  opacity: 0.5;
  position: fixed;
  left: 45vw;
  top: 40vh;
  animation-name: mouse;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@keyframes mouse {
  0% {left: 40vw; top: 35vh}
  50% {left: 50vw; top: 45vh}
  100% {left: 40vw; top: 35vh}
}

.animalContainer {
  height: 90vh;
  width: 100vw;
}

.duplicateContainer {
  height: 90vh;
  width: 100vw;
}

