.end h2 {
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  font-family: 'Amatic SC', cursive;
  cursor: pointer;
}

.end h3 {
  font-size: 60px;
  font-weight: 100;
  text-align: center;
  font-family: 'Amatic SC', cursive;
}

.end h4 {
  font-size: 40px;
  font-weight: 100;
  text-align: center;
  font-family: 'Amatic SC', cursive;
}

.deer {
  position: fixed;
  bottom: 0;
  left: -80px;
  height: 50vh;
}

.lion {
  position: fixed;
  bottom: -60px;
  right: 0;
  height: 50vh;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.score {
  font-size: 50px;
  font-family: 'Amatic SC', cursive;
  margin-right: 10px;
}

.time {
  font-size: 50px;
  font-family: 'Amatic SC', cursive;
  margin-left: 10px;
}

.title {
  font-size: 60px;
  font-family: 'Amatic SC', cursive;
}

.topBar {
  display: flex;
  justify-content: space-between;
  height: 70px;
}

.title {
  position: absolute;
  width: 100%;
  text-align: center;
}

.bug {
  width: 100px;
  position: fixed;
  cursor: crosshair;
}

.fish {
  width: 200px;
  position: fixed;
  z-index: 2;
}

.chips {
  width: 75px;
  position: fixed;
}

.fishContainer {
  display: flex;
  justify-content: center;
  height: 90vh;
  align-items: center;
}

.hiddenInput {
  opacity: 0;
  width: 100vw;
  height: 100vh;
  z-index: 3;
  position: fixed;
  cursor: none;
}

.keys {
  width: 400px;
  opacity: 0.5;
  animation-name: pulseKeys;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@keyframes pulseKeys {
  0% {width: 400px}
  50% {width: 500px}
  100% {width: 400px}
}

/* everything below here is for animation of overlay */

.overlayButton-CJ {
  font-size: 40px;
}

.overlay-CJ {
  position: relative;
  width: 200px;
  height: 200px;
  top: 40vh;
  left: 40vw;
}
.overlay-CJ::before {
  content: url(https://www.petage.com/wp-content/uploads/2019/09/Depositphotos_74974941_xl-2015-e1569443284386-670x627.jpg);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  font-size: 64px;
  background: rgba(0, 0, 0, 0.5);
}
.overlay-CJ:hover:before {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* everything below here is for animation of shake button */

.shake {
display: inline-block;
transform-origin: center center;
}
.shake:hover {
animation-name: shake-base;
animation-duration: 100ms;
animation-iteration-count: infinite;
animation-timing-function: ease-in-out;
animation-delay: 0s;
}
.shake.freez {
animation-name: shake-base;
animation-duration: 100ms;
animation-iteration-count: infinite;
animation-timing-function: ease-in-out;
animation-delay: 0s;
animation-play-state: paused;
}
.shake.freez.shake-hard {
